.ServiceHero{
    /* background-color: var(--bg-color); */
}

.service-banner {
    width: 100%;
    height: 46vw;
    max-height: 650px;
    background-color: rgb(41, 96, 236, 0.3);
    

    position: relative;
    padding: 3% 0;
} 

.bgg {
    background-image: url(./assets/bg-service.png);
    /* background-color: rgba(167, 167, 270, 0.4); */
    background-repeat: no-repeat;
    background-position: 37% bottom;
    background-size: contain;

    position: absolute;
    z-index: 5;

    top: 0;
    width: 100%;
    height: 100%;
}

.bgg2 {
    background-image: url(../../Homepage/Learn/assets/header.png);
    /* background-color: rgba(167, 167, 270, 0.4); */
    background-repeat: no-repeat;
    background-position: 37% bottom;
    background-size: cover;

    position: absolute;
    z-index: 5;

    top: 0;
    width: 100%;
    height: 100%;
}

.inner-banner {
    background-color: rgba(255, 255, 255, 0.3);
    height: 90%;
    width: 36%;
    margin: 0 32%;
    border-radius: 10px;
    z-index: 3;

    position: absolute;
}

.sCourse {
    background-color: white;
    width: max-content;
    padding: 7px;
    border-radius: 4px;
    box-shadow: 5px 5px 15px 1px rgba(0, 0, 0, 0.5);
    
    position: absolute;
    z-index: 6;

    font-size: clamp(12px, 1.6vw, 24px);
    transition: .7s;
    cursor: pointer;
    
    display: flex;
    align-items: center;
    gap: 4px;
}

.sCourse img {
    height: 30px;
}

.sCourse:nth-child(1) {
    top: 10%; left: 23%;
}
.sCourse:nth-child(2) {
    top: 10%; left: 60%;
}
.sCourse:nth-child(3) {
    top: 30%; left: 63%;
}
.sCourse:nth-child(4) {
    top: 80%; left: 55%;
}
.sCourse:nth-child(5) {
    top: 50%; left: 60%;
}
.sCourse:nth-child(6) {
    top: 80%; left: 24%;
}
.sCourse:nth-child(7) {
    top: 30%; left: 18%;
}
.sCourse:nth-child(8) {
    top: 50%; left: 21%;
}

.sCourse:hover {
    transform: scale(1.1);
}

.design-a {
    height: 9vw;
    width: 9vw;
    border-radius: 50%;
    background-color: red;
    background: linear-gradient(blue 10%,orange 100%);

    position: absolute;
    top: 21%;
    left: 28%;
}
.design-b {
    height: 9vw;
    width: 9vw;
    border-radius: 50%;
    background-color: red;
    background: linear-gradient(black 10%,blue 100%);

    position: absolute;
    bottom: 21%;
    right: 27%;
}

.service-stats {
    background-color: white;
    display: flex;
    width: 80%;
    margin: 20px auto;
    justify-content: space-between;
    padding: 25px 10px;
    border-radius: 3px;
    box-shadow: 0px 3px 5px 0px rgba(0, 0, 0, 0.1);

}

.service-stat {
    text-align: center;
    width: 31%;
    display: flex;
    justify-content: center;
    margin: auto;
}

.service-stat:nth-child(2) {
    border-left: 1px solid rgb(14, 14, 14, 0.1);
    border-right: 1px solid rgb(14, 14, 14, 0.1);
}

.service-stat p {
    color: rgba(14, 14, 44, 0.6);
    font-size: var(--p-font);
}

.service-stat img {
    height: 30px;
    width: 30px;
    background-color: rgb(0, 0, 255, 0.2);
    border-radius: 50%;
    padding: 3px;
    margin-right: 4px;
}

@media (max-width: 550px) {
.service-stat {
    flex-wrap: wrap;
}
.service-stat div {
    width: 100%;
    margin-top: 3px;
}

.service-stat img {
    background: none;
    border-radius: 0;
}

}

@media (max-width: 800px) {

    .service-banner {
        height: 400px;
    }

    .bgg2 {
        background-position: center;
    }

    .inner-banner {
        width: 60%;
        margin: 0 20%;
    }

    .sCourse:nth-child(1) {
        top: 10%; left: 15%;
    }
    .sCourse:nth-child(2) {
        top: 10%; left: 60%;
    }
    .sCourse:nth-child(3) {
        top: 30%; left: 63%;
    }
    .sCourse:nth-child(4) {
        top: 80%; left: 55%;
    }
    .sCourse:nth-child(5) {
        top: 50%; left: 60%;
    }
    .sCourse:nth-child(6) {
        top: 80%; left: 12%;
    }
    .sCourse:nth-child(7) {
        top: 30%; left: 12%;
    }
    .sCourse:nth-child(8) {
        top: 50%; left: 9%;
    }

    .service-stats {
        width: 100%;
        border-radius: 0;
        margin-top: 0;
    }
}
@media (max-width: 530px) {
.service-stat {
    flex-wrap: wrap;
}
.service-stat div {
    width: 100%;
    margin-top: 3px;
}
}

@media (max-width: 500px) {

    .sCourse {
        display: block;
        text-align: center;
        height: 75px;
        width: 100px;
    }

    .sCourse:nth-child(1) {
        top: 10%; left: 10%;
    }
    .sCourse:nth-child(2) {
        top: 10%; right: 10%; left: unset;  
    }
    .sCourse:nth-child(3) {
        top: 54%; right: 10%; left: unset;
    }
    .sCourse:nth-child(4) {
        top: 76%; right: 10%; left: unset;
    }
    .sCourse:nth-child(5) {
        top: 32%; right: 10%; left: unset;
    }
    .sCourse:nth-child(6) {
        top: 32%; left: 10%;
    }
    .sCourse:nth-child(7) {
        top: 54%; left: 10%;
    }
    .sCourse:nth-child(8) {
        top: 76%; left: 10%;
    }
}

@media (max-width: 361px) {
    .service-banner {
        /* height: 300px; */
    }

    .inner-banner {
        width: 80%;
        margin: 0 10%;
    }

   .sCourse {
    font-size: 10px;
   }
}

@media (min-width: 1440px) {
    .service-stats {
        height: 120px;
        font-size: 28px;
    }
}